<template>
  <div>
    <biddModal ref="biddModal" is-disabled :organizational-form="organizationalForm" :bill-type="billType" @refreshList="searchList" />
  </div>
</template>

<script>
import biddModal from '@/views/Implement/components/biddModal.vue';
export default {
  name: 'Bidding',
  components: { biddModal },
  data() {
    return {
      // 组织形式
      organizationalForm: {
        1: '公开',
        2: '竞争性磋商',
        3: '竞争性谈判'
      },
      // 招标类型
      billType: {
        1: '政采',
        2: '其他'
      }
    }
  },
  mounted() {

  },
  methods: {
    searchList() {},
    search(id) {
      this.$nextTick(() => {
        this.$refs.biddModal.showModal(id);
      })
    }
  }
}
</script>



