<template>
  <div>
    <!-- 弹出框 -->
    <Modal
      v-model="popUp"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        审计报告
      </p>
      <Tabs
        :animated="false"
        class="innerTabStyle"
        value="name1"
      >
        <TabPane
          label="信息设置"
          name="name1"
        >
          <Form
            ref="form"
            :model="form"
            :label-width="80"
          >
            <Row>
              <Col span="11">
              <FormItem
                label="报告名称"
              >
                <Input
                  v-model="form.reportName"
                  disabled
                ></Input>
              </FormItem>
              </Col>
              <Col
                span="11"
                offset="2"
              >
              <FormItem
                label="审计机构"
              >
                <Input
                  v-model="form.auditOrgName"
                  disabled
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="11">
              <FormItem
                label="审计时间"
              >
                <DatePicker v-model="form.reportAuditTime" type="datetime" format="yyyy-MM-dd HH:mm" :transfer="true" disabled style="width:100%;"></DatePicker>
              </FormItem>
              </Col>
              <Col
                span="11"
                offset="2"
              >
              <FormItem
                label="审计结果"
              >
                <Input
                  v-model="form.auditResult"
                  disabled
                ></Input>
              </FormItem>
              </Col>
            </Row>
          </Form>
        </TabPane>
        <TabPane
          label="附件"
          name="name2"
        >
          <uploadFile ref="uploadFile" is-disabled :form="form" />
        </TabPane>
      </Tabs>
      <p
        slot="footer"
        style="text-align: left"
      >
        <Button icon="md-print">打印</Button>
        <Button icon="md-exit">导出</Button>

      </p>
    </Modal>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
export default {
  name: 'AuditReport',
  components: {
    uploadFile
  },
  props: {
    searchData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      form: {
        reportName: '',
        auditOrgName: '',
        reportAuditTime: '',
        auditResult: ''
      },
      popUp: false
    }
  },
  methods: {
    search() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxGetUtil('/auditReport/searchAuditReport/' + that.searchData.reportId, {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          if (data) {
            that.form = data
            that.form.reportAuditTime = that.$moment(that.form.reportAuditTime).format('YYYY年MM月DD日 HH:mm:ss')
            that.form.fileids = data.reportFileids
          }
          this.popUp = true
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    }
  }
}
</script>



