<template>
  <div>
    <!--位置 实施方案弹出框-->
    <implementPlan ref="implementPlan" :search-data="searchData" />
    <!--位置 投资汇总弹出框-->
    <investSummary ref="investSummary" :search-data="searchData" />
    <!-- 审计报告 -->
    <auditReport ref="auditReport" :search-data="searchData" />
    <!-- 档案信息 -->
    <archive ref="archive" :search-data="searchData" />
    <!-- 合同信息 -->
    <contract ref="contract" :contract-id="searchData.id" />
    <!-- 票据管理 -->
    <billInfo ref="billInfo" />
    <!-- 专帐信息 -->
    <account ref="account" />
    <!-- 招投标 -->
    <biddModal ref="biddModal" />
  </div>
</template>

<script>
import implementPlan from '@/components/implementPlan.vue'
import investSummary from '@/components/investSummary.vue'
import auditReport from '@/components/auditReport.vue'
import archive from '@/components/archive.vue'
import contract from '@/components/contract.vue'
import billInfo from '@/components/billInfo.vue'
import account from '@/components/account.vue'
import biddModal from '@/components/biddModal.vue'
export default {
  name: 'AuditModal',
  components: {
    implementPlan,
    investSummary,
    auditReport,
    archive,
    contract,
    billInfo,
    account,
    biddModal
  },
  data() {
    return {
      searchData: {
        auditBatchType: null,
        relationId: null,
        adminId: null,
        auditStatus: null,
        auditBatchId: null
      }
    }
  },
  methods: {
    showModal(row) {
      const auditBatchType = row.auditBatchType
      const relationId = row.relationId
      this.searchData = {
        auditBatchType: auditBatchType,
        adminId: row.adminId,
        auditStatus: row.auditStatus,
        auditBatchId: row.auditBatchId
      }
      if (auditBatchType === 1) {
        // 投资汇总
        this.searchData.projectId = relationId
        this.$nextTick(() => {
          this.$refs.investSummary.search();
        })
      } else if (auditBatchType === 2) {
        // 实施方案
        this.searchData.isOwnProject = true
        this.searchData.projectId = relationId
        this.$nextTick(() => {
          this.$refs.implementPlan.search();
        })
      } else if (auditBatchType === 3) {
        // 票据管理
        this.$nextTick(() => {
          this.$refs.billInfo.getBillInfoList(relationId);
        })
      } else if (auditBatchType === 4) {
        // 审计报告
        this.searchData.reportId = relationId
        this.$nextTick(() => {
          this.$refs.auditReport.search();
        })
      } else if (auditBatchType === 5) {
        // 专账信息
        this.$nextTick(() => {
          this.$refs.account.search(relationId);
        })
      } else if (auditBatchType === 6) {
        // 档案信息
        this.searchData.id = relationId
        this.$nextTick(() => {
          this.$refs.archive.search();
        })
      } else if (auditBatchType === 7) {
        // 档案信息
        this.searchData.id = relationId
        this.$nextTick(() => {
          this.$refs.contract.search();
        })
      } else if (auditBatchType === 8) {
        // 招投标信息
        this.$nextTick(() => {
          this.$refs.biddModal.search(relationId);
        })
      }
    }
  }
}
</script>



