<template>
  <div>
    <!-- 弹出框 -->
    <constructionModel ref="constructionModel" :contract-id="contractId" :sign-gist="signGist" :contract-attr="contractAttr" :budget-gist="budgetGist" is-disabled></constructionModel>

    <setUpModel ref="setUpModel" :contract-id="contractId" :sign-gist="signGist" :contract-attr="contractAttr" :budget-gist="budgetGist" is-disabled></setUpModel>

    <entrustModel ref="entrustModel" :contract-id="contractId" :sign-gist="signGist" :contract-attr="contractAttr" :budget-gist="budgetGist" is-disabled></entrustModel>
  </div>
</template>

<script>
import constructionModel from '@/views/contractManage/components/construction';
import setUpModel from '@/views/contractManage/components/setUp';
import entrustModel from '@/views/contractManage/components/entrust';
export default {
  name: 'Contract',
  components: {
    constructionModel,
    setUpModel,
    entrustModel
  },
  props: {
    contractId: {
      type: Number,
      default: null
    }
  },
  data() {
    return {
      // 签订依据
      signGist: {
        1: '比选',
        2: '招标',
        3: '其他'
      },
      // 预算依据
      budgetGist: {
        1: '土地平整、消防池及水系挖土登',
        2: '规划设计费',
        3: '勘察费',
        4: '建设管理费',
        5: '土地使用费'
      },
      // 合同类型
      contractTypeList: {
        1: {
          modal: 'constructionModel',
          name: '建设工程施工合同'
        },
        2: {
          modal: 'setUpModel',
          name: '设备购置合同'
        },
        3: {
          modal: 'entrustModel',
          name: '委托业务合同'
        }
      },
      // 合同属性
      contractAttr: {
        1: '新签合同',
        2: '补充合同'
      }
    }
  },
  mounted() {

  },
  methods: {
    search() {
      const that = this
      that.$Spin.show();
      that.commonUtil.ajaxGetUtil('/contract/searchContractInfo/' + that.contractId, {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          that.selectModal(data.contractType)
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    selectModal(type) {
      const name = this.contractTypeList[type].modal
      this.$nextTick(() => {
        this.$refs[name].showModal(type);
      })
    }
  }
}
</script>



