<template>
  <div>
    <!-- 弹出框 -->
    <Modal
      v-model="isPopUp"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        档案信息
      </p>
      <Tabs
        :animated="false"
        class="innerTabStyle"
        value="name1"
      >
        <TabPane
          label="信息设置"
          name="name1"
        >
          <Form
            ref="form"
            :model="form"
            :label-width="80"
          >
            <Row>
              <Col span="24">
              <FormItem
                label="文件名称"
              >
                <Input
                  v-model.trim="form.name"
                  disabled
                  clearable
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="卷名"
              >
                <Select
                  v-model="form.juan_name"
                  disabled
                  transfer
                  clearable
                >
                  <Option v-for="item in volumeList" :key="item.adminMenuId" :value="parseInt(item.adminMenuId)">{{ item.adminMenuName }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="案卷题名"
              >
                <Select
                  v-model="form.anjuan_timin"
                  disabled
                  transfer
                  clearable
                >
                  <Option v-for="item in titleList" :key="item.adminMenuId" :value="parseInt(item.adminMenuId)">{{ item.adminMenuName }}</Option>
                </Select>
              </FormItem>
              </Col>
            </Row>
            <Col span="24">
            <FormItem label="备注">
              <Input
                v-model.trim="form.remark"
                disabled
                type="textarea"
                :autosize="{minRows: 1,maxRows: 5}"
              ></Input>
            </FormItem>
            </Col>
          </Form>
        </TabPane>
        <TabPane
          label="附件"
          name="name7"
        >
          <uploadFile ref="uploadFile" is-disabled :form="{fileids: form.archivesFileids}" />
        </TabPane>
      </Tabs>
    </Modal>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
export default {
  name: 'Archive',
  components: {
    uploadFile
  },
  props: {
    searchData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      form: {
        anjuan_timin: '',
        juan_name: '',
        fileTitle: '',
        name: '',
        remark: ''
      },
      isPopUp: false,
      menuList: sessionStorage.getItem('menuList') ? JSON.parse(sessionStorage.getItem('menuList')) : [],
      volumeList: [],
      titleList: []
    }
  },
  mounted() {
    this.getSelectList()
  },
  methods: {
    // 卷名
    getSelectList() {
      const menuList = this.menuList
      const volumeList = []// 卷名
      const titleList = []// 案卷题名
      for (const item of menuList) {
        if (item.isVolumeName === 1) {
          const menuModelList = item.menuModelList
          item.adminMenuName = item.adminMenuName.replace(/管理/g, '文件')
          volumeList.push(item)
          if (menuModelList.length > 0) {
            titleList.push.apply(titleList, menuModelList);
          }
        }
      }
      this.volumeList = volumeList;
      this.titleList = titleList;
    },
    search() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxGetUtil('/archives/searchArchives/' + that.searchData.id, {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        that.isPopUp = true
        if (res.code === 1) {
          that.form = res.data
          that.$nextTick(() => {
            that.$refs.uploadFile.getFileItems()
          })
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    }
  }
}
</script>



