<template>
  <div>
    <!-- 弹出框 -->
    <Modal
      v-model="popUp"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">票据信息</p>
      <Tabs
        v-model="tabName"
        :animated="false"
        class="innerTabStyle"
      >
        <TabPane
          label="基本信息"
          name="name1"
        >
          <Form
            ref="form"
            :model="form"
            :label-width="80"
          >
            <Row>
              <Col span="12">
              <FormItem
                label="合同名称"
                prop="contractid"
              >
                <Select
                  v-model="form.contractid"
                  disabled
                  transfer
                  clearable
                >
                  <Option v-for="item in contractList" :key="item.contractId" :value="parseInt(item.contractId)">{{ item.contractName }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="乙方名称"
                prop="partyBName"
              >
                <Input
                  v-model="form.partyBName"
                  disabled
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="发票编号"
                prop="code"
              >
                <Input
                  v-model="form.code"
                  disabled
                ></Input>
              </FormItem>
              </Col>
              <Col
                span="12"
              >
              <FormItem
                label="发票类别"
                prop="type"
              >
                <Select
                  v-model="form.type"
                  disabled
                  transfer
                  filterable
                  clearable
                  placeholder="请选择发票类别"
                >
                  <Option v-for="(item,key) in typeList" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="销售类别"
                prop="sellType"
              >
                <Select
                  v-model="form.sellType"
                  disabled
                  transfer
                  filterable
                  clearable
                >
                  <Option v-for="(item,key) in sellList" :key="key" :value="parseInt(key)">{{ item }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="发票金额"
                prop="money"
              >
                <Input
                  v-model="form.money"
                  disabled
                  number
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem
                label="开票日期"
                prop="invoiceTime"
              >
                <DatePicker v-model="form.invoiceTime" disabled type="date" style="width:100%;" transfer></DatePicker>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem label="备注">
                <Input
                  v-model="form.remark"
                  disabled
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                ></Input>
              </FormItem>
              </Col>
            </Row>
          </Form>
        </TabPane>
        <TabPane
          label="发票内容"
          name="name8"
        >
          <!-- table-content -->
          <Content
            class="content"
            :style="{padding: '14px 24px',background: '#fff'}"
          >
            <Table :columns="columns" :data="billInfoList" />
            <!-- <bottomTotal name="总投资额" :sum="totalVolume" /> -->
          </Content>
        </TabPane>
        <TabPane
          label="附件"
          name="name7"
        >
          <uploadFile ref="uploadFile" is-disabled :form="{fileids: form.billFileids}" />
        </TabPane>
      </Tabs>
    </Modal>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
export default {
  name: 'Template',
  components: {
    uploadFile
  },
  data() {
    return {
      popUp: false,
      contractList: [], // 合同名称列表
      sellList: {
        0: '建筑工程服务',
        1: '机械设备购置',
        2: '其他'
      },
      typeList: {
        0: '普票',
        1: '专票'
      },
      tabName: 'name1',
      form: {
        contractid: '',
        partyBName: '',
        code: '',
        invoiceType: '',
        sellType: '',
        money: null,
        invoiceTime: '',
        remark: ''
      },
      billInfoList: [],
      // 发票内容
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left'
        },
        {
          title: '货物或应税劳务,服务名称',
          key: 'name'
        },
        {
          title: '规格型号',
          key: 'model'
        },
        {
          title: '单位',
          key: 'danWei'
        },
        {
          title: '数量',
          key: 'number'
        },
        {
          title: '含税单位',
          key: 'hanShuiDanWei'
        },
        {
          title: '含税金额',
          key: 'hanShuiMoney'
        }
      ]
    }
  },
  mounted() {
    this.getContractList()
  },
  methods: {
    // 获取合同信息列表
    getContractList() {
      const that = this
      that.$Spin.show();
      that.commonUtil.ajaxPostUtil('/contract/searchContractList', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.contractList = res.data.contractList
        } else {
          that.$Message.error(res.msg);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    clearData() {
      this.form = {
        contractid: '',
        partyBName: '',
        code: '',
        invoiceType: '',
        sellType: '',
        money: null,
        invoiceTime: '',
        remark: ''
      }
      this.tabName = 'name1'
      this.$refs.form.resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    },
    // 获取票据相关的发票信息
    getBillInfoList(id) {
      const that = this;
      that.clearData();
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxGetUtil('/bill/searchBillInfo/' + id, {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          data.invoiceTime = new Date(data.invoiceTime)
          that.form = data
          that.billInfoList = res.data.billList
          that.$nextTick(() => {
            that.$refs.uploadFile.getFileItems()
            that.popUp = true
          })
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    }
  }
}
</script>



