<template>
  <div>
    <!-- 收入 -->
    <IncomeModel ref="income" :jie-or-dai="jieOrDai" :ke-mu-array="keMuArray" is-disabled :contract-type="incomeType" @refreshList="searchList"></IncomeModel>
    <!-- 支出 -->
    <PayModel ref="pay" :jie-or-dai="jieOrDai" :ke-mu-array="keMuArray" is-disabled :contract-type="payType" :contract-list="contractList" :yi-fang-list="yiFangList" @refreshList="searchList"></PayModel>
  </div>
</template>

<script>
import IncomeModel from '@/views/capitalManage/components/Accountinfo/income';
import PayModel from '@/views/capitalManage/components/Accountinfo/pay';
export default {
  name: 'Account',
  components: {
    IncomeModel,
    PayModel
  },
  data() {
    return {
    // 收入和支出
      contractArray: {
        1: 'income',
        2: 'pay'
      },
      keMuArray: {
        1: '在建工程',
        2: '费用',
        3: '其他'
      },
      // 借或贷
      jieOrDai: {
        1: '借',
        2: '贷'
      },
      incomeType: {
        1: '应收',
        2: '实收'
      },
      payType: {
        1: '应付',
        2: '实付'
      },
      // 合同名称列表
      contractList: [],
      // 往来单位
      yiFangList: []
    }
  },
  created() {
    this.getYiFangInfo()
    // 获取合同名称 列表
    this.getContractList();
  },
  methods: {
    searchList() {},
    search(id) {
      const that = this;
      that.$Spin.show();
      that.commonUtil.ajaxGetUtil('/specialAccount/seatchSpecialAccountById/' + id, {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          const data = res.data
          const type = data.shouzhi_type
          if (type) {
            data.date = new Date(data.date)
            that.$nextTick(() => {
              that.$refs[that.contractArray[type]].showModal(data);
            })
          } else {
            that.$Message.error('获取信息失败');
          }
        } else {
          that.$Message.error(res.msg);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 获取乙方信息列表
    getYiFangInfo() {
      const that = this;
      that.$Spin.show();
      that.commonUtil.ajaxGetUtil('/contract/searchYiFang', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.yiFangList = res.data
        } else {
          that.$Message.error(res.msg);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 获取合同信息列表
    getContractList() {
      const that = this
      that.$Spin.show();
      that.commonUtil.ajaxPostUtil('/contract/searchContractList', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.contractList = res.data.contractList
        } else {
          that.$Message.error(res.msg);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    }
  }
}
</script>



