<template>
  <div>
    <!---->
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>审核管理
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon> 未审核信息
      </BreadcrumbItem>
      <span class="screenContent">
        <img
          style="width: 11px;margin-right: 8px;margin-left: 10px;"
          src="../assets/day.png"
        >
        {{ curTime }}</span>
      <span class="screenContent">
        <img
          style="width: 10.5px;margin-right: 8px"
          src="../assets/screen.png"
        >
        <a
          href="javascript:void(0)"
          @click="isShowScreen"
        >
          筛选
          <Icon type="ios-arrow-down"></Icon>
        </a>
      </span>
    </Breadcrumb>
    <Content
      v-if="isScreen"
      class="content isScreen"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="85"
      >
        <Row>
          <Col span="10">
          <FormItem label="承担单位">
            <Select
              v-model="searchData.chendanOrgId"
              filterable
              clearable
              :transfer="true"
              placeholder="请选择承担单位"
            >
              <Option
                v-for="item in companyList"
                :key="item.orgId"
                :value="item.orgId"
              >{{ item.orgName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            span="10"
            offset="1"
          >
          <FormItem label="项目名称">
            <Input
              v-model="searchData.projectName"
              placeholder="请填写项目名称"
            ></Input>
          </FormItem>
          </Col>
        </Row>
        <!-- 提交开始和结束 -->
        <Row style="margin-top:15px;">
          <Col span="10">
          <FormItem label="提交开始时间">
            <DatePicker
              v-model="searchData.submitStartTime"
              style="width:100%;"
              type="date"
              :options="startTimeOptions"
              placeholder="请选择提交开始时间"
              transfer
              placement="bottom-start"
              @on-change="startTimeChange"
            ></DatePicker>
          </FormItem>
          </Col>
          <Col span="10" offset="1">
          <FormItem label="提交结束时间">
            <DatePicker
              v-model="searchData.submitEndTime"
              style="width:100%;"
              type="date"
              transfer
              :options="endTimeOptions"
              placeholder="请选择提交结束时间"
              placement="bottom-start"
              @on-change="endTimeChange"
            ></DatePicker>
          </FormItem>
          </Col>
          <Col
            style="float:right"
            span="1"
          >
          <Button type="primary" @click="fetchNoPassList">确认</Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button icon="md-print" @click.stop="printContent(7)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/audit/exportNotAuditListExcel')">
        导出
      </Button>
      <Table
        ref="checkTable"
        highlight-row
        class="table"
        :columns="columns"
        :data="data"
      ></Table>
      <Page
        style="text-align: right"
        show-sizer
        transfer
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
    </Content>
    <!--详情弹出框-->
    <Modal
      v-model="checkModel"
      class="selStyle"
      :mask-closable="false"
      width="75%"
    >
      <p slot="header">
        <span>请填写审核信息</span>
      </p>
      <Tabs
        :animated="false"
        class="innerTabStyle"
        value="name1"
      >
        <TabPane
          label="信息设置"
          name="name1"
        >
          <Form
            ref="form"
            :model="form"
            :rules="formRules"
            :label-width="80"
          >
            <Col span="24">
            <FormItem
              label="审核结果"
              prop="auditStatus"
            >
              <RadioGroup v-model="form.auditStatus">
                <Radio :label="1">通过</Radio>
                <Radio :label="2">未通过</Radio>
              </RadioGroup>
            </FormItem>
            </Col>
            <Col span="24">
            <FormItem label="审核意见">
              <Input
                v-model.trim="form.auditMessage"
                type="textarea"
                :autosize="{minRows: 1,maxRows: 5}"
                placeholder="请填写审核意见"
              ></Input>
            </FormItem>
            </Col>
          </Form>
        </TabPane>
        <TabPane
          class="uploadStyle"
          label="附件"
          name="name7"
        >
          <uploadFile ref="uploadFile" :is-disabled="false" :form="form" />
        </TabPane>
      </Tabs>
      <p slot="footer">
        <Button
          style="float: left"
          icon="md-print"
        >
          打印
        </Button>
        <Button
          style="float: left"
          icon="md-exit"
        >
          导出
        </Button>
        <Button
          type="primary"
          @click="submitCheck('form')"
        >
          确认
        </Button>
        <Button
          style="margin-left: 8px"
          @click="resetCheck('form')"
        >
          取消
        </Button>
      </p>
    </Modal>
    <!-- 弹出框集合 -->
    <AuditModal ref="modalList" />
  </div>
</template>

<script>
import { dateformat } from '@/filters/index.js'
import AuditModal from '@/components/AuditModal.vue'
import uploadFile from '@/components/fileUpload.vue'
import excelExcel from '@/mixins/exportExcel.js'
export default {
  name: 'NoCheck',
  components: {
    AuditModal,
    uploadFile
  },
  mixins: [excelExcel],
  data() {
    return {

      // 提交搜索
      startTimeOptions: {}, // 开始日期设置
      endTimeOptions: {}, // 结束日期设置
      total: 0,
      transferValue: true,
      // 位置弹出框
      innerData: [],
      innerDataYu: [],
      innerDataG: [],
      innerDataYi: [],
      innerDataOther: [],
      innerDataTotal: [],
      // 建筑安装工程
      innerColumns: [],
      // 预备费表格
      innerColumnsYu: [],
      // 工程建设其他费用
      innerColumnsG: [],
      // 仪器设备购置
      innerColumnsYi: [],
      // 其他配套工程
      innerColumnsOther: [],
      // 投资汇总表
      innerColumnsTotal: [],
      editIndex: -1, // 当前聚焦的输入框的行数
      // 审核填写模态框
      checkModel: false,
      form: {
        auditBatchId: null,
        projectId: null,
        // 审核状态
        auditStatus: null,
        // 审核意见
        auditMessage: '',
        fileIds: null
      },
      formRules: {
        auditStatus: [
          { type: 'number', required: true, message: '请选择审核状态', trigger: 'change' }
        ]
      },
      // 筛选内容
      searchData: {
        // 承担单位
        chendanOrgId: '',
        // 项目名称
        projectName: '',
        // 提交时间
        submitEndTime: null,
        submitStartTime: null,
        // 默认第一页
        orderBy: 'audit_batch_createtime',
        ascending: false,
        pageNo: 1,
        pageSize: 10,
        auditStatus: 0
      },
      isScreen: false,
      curTime: '',
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left'
        },
        {
          title: '提交时间',
          key: 'createTime',
          render: (h, params) => {
            return h('div',
              dateformat(params.row.createTime, 'YYYY-MM-DD HH:mm:ss')
            )
          }
        },
        {
          title: '承担单位',
          key: 'chendanOrg'
        },
        {
          title: '项目名称',
          key: 'projectName'
        },
        {
          title: '位置',
          key: 'auditLocation',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showLocationInfo(params.row)
                }
              }
            }, params.row.auditLocation);
          }
        },
        {
          align: 'center',
          title: '审核状态',
          key: 'action',
          fixed: 'right',
          width: 120,
          render: (h, params) => {
            const row = params.row
            const audit = this.status[row.auditStatus]
            return h('div', [
              h('Button', {
                style: {
                  color: audit.color + '!important'
                },
                props: {
                  type: 'text',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.form.auditBatchId = row.auditBatchId
                    this.checkModel = true
                  }
                }
              }, audit.name)
            ]);
          }
        }
      ],
      data: [
      ],
      // 承担单位
      companyList: [],
      status: {
        0: {
          name: '正在审核',
          color: 'blue'
        },
        1: {
          name: '审核通过',
          color: 'green'
        },
        2: {
          name: '审核未通过',
          color: 'red'
        },
        3: {
          name: '未审核',
          color: 'orange'
        }
      }
    }
  },
  mounted() {
    this.fetchNoPassList();
    this.getCompanyList();
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
  },
  methods: {
    // 开始时间和结束时间
    startTimeChange: function(e) { // 设置开始时间
      this.searchData.submitStartTime = e;
      this.endTimeOptions = {
        disabledDate: date => {
          const startTime = e ? new Date(e).valueOf() : null;
          return date && (date.valueOf() < startTime);
        }
      }
    },
    endTimeChange: function(e) { // 设置结束时间
      // 23:59:59
      this.searchData.submitEndTime = e;
      const endTime = e ? new Date(e).valueOf() - 24 * 3600000 : null;
      this.startTimeOptions = {
        disabledDate(date) {
          return date && date.valueOf() > endTime;
        }
      }
    },
    // 承担单位
    getCompanyList() {
      const that = this;
      that.$Spin.show();
      that.commonUtil.ajaxPostUtil('/organization/searchOrganization/list', { orgType: '', orgName: '' }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.companyList = res.data.orgList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    pageNoChange(pageNo) {
      this.searchData.pageNo = pageNo
      this.fetchNoPassList()
    },
    pageSizeChange(pageSize) {
      this.searchData.pageSize = pageSize
      this.fetchNoPassList()
    },
    // 重组数据
    getNewSearchData() {
      const searchData = Object.assign({}, this.searchData)
      const endTime = searchData.submitEndTime
      const startTime = searchData.submitStartTime
      if (endTime) {
        searchData.submitEndTime = new Date(endTime + ' 23:59:59').getTime()
      }
      if (startTime) {
        searchData.submitStartTime = new Date(startTime + ' 00:00:00').getTime()
      }
      return searchData
    },
    // 获取未审核数据
    fetchNoPassList() {
      const that = this;
      const searchData = that.getNewSearchData()
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/audit/list', searchData, res => {
        if (res.code === 1) {
          const data = res.data;
          that.total = data.totalCount;
          that.data = data.auditBatchList;
        } else {
          that.$Message.error(res.msg);
        }
        // 关闭数据加载
        that.$Spin.hide();
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.commonUtil.getAuditCountApi(that)
    },
    // 点击“位置”
    showLocationInfo(row) {
      this.$nextTick(() => {
        this.$refs.modalList.showModal(row);
      })
    },
    // 确定审核
    submitCheck(name) {
      const that = this;
      const form = that.form;
      form.fileIds = that.$refs.uploadFile.getFileIds()
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxPutUtil('/auditBatch/updateAuditBatch', form, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.checkModel = false;
              that.$Message.success(res.msg);
              that.fetchNoPassList()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        }
      })
    },
    resetCheck(name) {
      this.form = {
        // 审核状态
        auditStatus: null,
        // 审核意见
        auditMessage: ''
      }
      this.$refs[name].resetFields();
      this.checkModel = false;
    },
    // 筛选按钮
    isShowScreen() {
      this.isScreen = !this.isScreen;
    }
  }
}
</script>



